
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapState, mapActions } from 'vuex'

export default {
name: 'App',
directives: {
TransferDom
},
data () {
return {
drawerVisibility: false,
showModeValue: 'push',
showPlacementValue: 'left',
navs: [
{
name: '欢迎页',
href: '/'
},
{
name: '基本信息',
href: '/base'
},
{
name: '个人能力',
href: '/can'
},
{
name: '工作经历',
href: '/expe'
},
{
name: '教育背景',
href: '/edu'
}
]
}
},
components: {
Radio,
Group,
Cell,
Badge,
Drawer,
ButtonTab,
ButtonTabItem,
ViewBox,
XHeader,
Tabbar,
TabbarItem,
Loading,
Actionsheet
},
methods: {
changeMenu: function(){
console.log(this.route);
return this.drawerVisibility = !this.drawerVisibility
}
},
computed: {
...mapState({
route: state => state.route,
path: state => state.route.path,
isLoading: state => state.vux.isLoading,
direction: state => state.vux.direction
}),
viewTransition () {
if (!this.direction) return ''
return 'vux-pop-' + (this.direction === 'forward' ? 'in' : 'out')
},
headerTransition () {
if (!this.direction) return ''
return this.direction === 'forward' ? 'vux-header-fade-in-right' : 'vux-header-fade-in-left'
},
leftOptions () {
return {
showBack: this.route.path !== '/'
}
},
isShowNav () {

return true
},
title () {
if (this.route.path === '/') return '个人简历'
if (this.route.path === '/base') return '基本信息'
if (this.route.path === '/can') return '个人能力'
if (this.route.path === '/expe') return '工作经历'
if (this.route.path === '/edu') return '教育背景'
}
},
watch: {

}
}



