import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import myBase from '@/components/base'
import can from '@/components/can'
import expe from '@/components/expe'
import edu from '@/components/edu'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/base',
      name: 'base',
      component: myBase
    },
    {
      path: '/can',
      name: 'can',
      component: can
    },
    {
      path: '/expe',
      name: 'expe',
      component: expe
    },
    {
      path: '/edu',
      name: 'edu',
      component: edu
    }
  ]
})



